<template>
  <div class="center">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span class="xg">/</span>
      </template>
      <template #content v-if="user">
        <div class="handle">
          <img :src="user.avatar_url" alt="" />
          <p>{{ user.loginname }}</p>
        </div>
        <p class="score">{{ user.score }}积分</p>
        <a class="github" href="https://github.com/"
          ><i class="iconfont icon-github-fill"></i>@{{
            user.githubUsername
          }}</a
        >
        <p class="time">注册时间{{ user.create_at | moment("from", "now") }}</p>
      </template>
    </Panel>
    <div class="second">
      <Panel>
        <template #header>
          <p>最近创建的话题</p>
        </template>
        <template #content v-if="user">
          <TopicsItem
            v-for="item in recent_topics.slice(0, 5)"
            :key="item.id"
            :item="item"
          />
        </template>
        <template #footer>
          <router-link :to="{ name: 'more' }" class="more"
            >查看更多>></router-link
          >
        </template>
      </Panel>
    </div>
    <div class="third">
      <Panel>
        <template #header>
          <p>最近参与的话题</p>
        </template>
        <template #content v-if="user">
          <TopicsItem
            v-for="item in recent_replies"
            :key="item.id"
            :item="item"
          />
        </template>
      </Panel>
    </div>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import { mapState, mapActions } from "vuex";
import TopicsItem from "../components/TopicsItem.vue";
export default {
  components: { Panel, TopicsItem },
  props: ["loginname"],
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    };
  },
  watch: {
    loginname: {
      immediate: true,
      handler(newValue) {
        this.getUser(newValue);
      },
    },
    user: {
      async handler(newValue) {
        const res = await this.$axios.all(
          newValue.recent_replies.map((el) =>
            this.$axios.get(`/topic/${el.id}`)
          )
        );
        const res2 = await this.$axios.all(
          newValue.recent_topics.map((el) => this.$axios.get(`/topic/${el.id}`))
        );
        this.recent_replies = res.map((el) => el.data);
        this.recent_topics = res2.map((el) => el.data);
        // console.log(this.recent_topics);
      },
    },
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
  methods: {
    ...mapActions(["getUser"]),
  },
};
</script>

<style lang="less">
.center {
  a {
    color: #409eff;
  }
  .xg {
    color: #ccc;
    margin-left: 8px;
  }
  .github {
    color: #778087;
  }
  .handle {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    img {
      width: 40px;
      height: 40px;
      border-radius: 3px;
    }
    p {
      color: #778087;
      font-size: 14px;
      margin-left: 8px;
    }
  }
  p {
    margin: 0;
  }
  .score {
    font-size: 14px;
    margin-bottom: 10px;
  }
  .github {
    color: #778087;
    font-size: 14px;
  }
  .time {
    color: #778087;
    margin-top: 8px;
  }
  .second {
    margin-top: 20px;
    a {
      color: #007fff;
      font-size: 14px;
    }
  }
  .third {
    a {
      font-size: 14px;
    }
  }
  .more {
    color: rgba(0, 0, 0, 0.733) !important;
  }
}
</style>
